 /* button */ 
  


  .button-container {
    display: flex;
    justify-content: center; /* Â∞áÂÖßÂÆπÊ∞¥Âπ≥ÁΩÆ‰∏≠ */
    align-items: center; /* Â∞áÂÖßÂÆπÂûÇÁõ¥ÁΩÆ‰∏≠ */
    margin-top: -30rem;
    margin-bottom: 20rem;
  }

  .btn {
    position: relative;
    min-width: 300px;
    border: 3px solid #C4D7B2;
    border-radius: 30px;
    background: transparent;
    color: #C4D7B2;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    transition: border 0.5s ease-out;
    transition-delay: 0.8s;
    overflow: hidden;
    transition: 0.5s;
    padding: 20px 40px;

    margin-top: 50px; /* Ë™øÊï¥ÊåâÈàïÁµÑÁöÑ‰∏äÈÇäË∑ù */
    margin: 0 30px; /* Ë™øÊï¥ÊåâÈàï‰πãÈñìÁöÑÊ∞¥Âπ≥ÈñìË∑ù */



}
.btn:before,
.btn:after {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    margin: auto;
    content: 'Button';
    border-radius: 50%;
    display: block;
    width: 22em;
    height: 22em;
    left: -5.5em;
    text-align: center;
    transition: box-shadow 0.5s ease-out;
    transition-delay: 0.75s;
}
.btn:after {
    transition-delay: 0.25s;
}
.btn span {
    position: relative;
    z-index: 1;
}
.btn:hover {
    color: #FFFF;
    border-color: #A0C49D;
    animation: anim 1.5s ease;
    transition: border 0.2s ease;
}
.btn:hover::before {
    box-shadow: inset 0 0 0 11em #C4D7B2;
    transition-delay: 0.05s;
}
.btn:hover::after {
    box-shadow: inset 0 0 0 11em #A0C49D;
    transition-delay: 0.5s;
}
@keyframes anim {
    0% { color: #E1ECC8; }
    50% { color: #515E63; }
    100% { color: #ffffff; }
}
  